import { __decorate } from "tslib";
import Vue from 'vue';
import { Mixin, Mixins } from 'vue-mixin-decorator';
import { suppliersMapper } from '../module/store';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
const Mappers = Vue.extend({
    computed: {
        ...suppliersMapper.mapGetters(['loadingError'])
    }
});
let MsgBoxTools = class MsgBoxTools extends Mixins(Mappers, MsgBoxToolsApp) {
    constructor() {
        super(...arguments);
        this.requestStatus = 'failFetchList';
        this.titles = { 'success': '!', 'fail': '!', 'other': ' ?' };
        this.statuses = {
            success: ['successFetchList', 'successEdit', 'successFetchIdentity', 'successResetSmsTryCount', 'successResetSmsSendCount', 'successDeleteIdentity'],
            fail: ['failFetchList', 'failEdit', 'failFetchIdentity', 'failResetSmsTryCount', 'failResetSmsSendCount', 'failDeleteIdentity'],
            other: ['beforeDeleteIdentity']
        };
        this.btns = {
            successFetchList: { firstBtn: '' },
            successEdit: { firstBtn: ' ', secondBtn: '  ' },
            successFetchIdentity: { firstBtn: '' },
            successResetSmsTryCount: { firstBtn: '' },
            successResetSmsSendCount: { firstBtn: '' },
            successDeleteIdentity: { firstBtn: '' },
            failFetchList: { firstBtn: ' ', secondBtn: '  ' },
            failEdit: { firstBtn: ' ', secondBtn: '' },
            failFetchIdentity: { firstBtn: ' ', secondBtn: '' },
            failResetSmsTryCount: { firstBtn: ' ', secondBtn: '' },
            failResetSmsSendCount: { firstBtn: ' ', secondBtn: '' },
            failDeleteIdentity: { firstBtn: ' ', secondBtn: '' },
            beforeDeleteIdentity: { firstBtn: '', secondBtn: '' }
        };
        this.msgBoxMsgSuccess = {
            successFetchList: '  ',
            successFetchIdentity: '  ',
            successEdit: '  ',
            successResetSmsTryCount: '   sms  ',
            successResetSmsSendCount: '  sms  ',
            successDeleteIdentity: '  '
        };
        this.msgBoxMsgOther = {
            beforeDeleteIdentity: '       '
        };
    }
    get statusType() {
        for (const type in this.statuses) {
            if (this.statuses[type].indexOf(this.requestStatus) >= 0)
                return type;
        }
    }
    get msgBoxTitle() { return this.titles[this.statusType]; }
    get msgBoxMsg() {
        if (this.statusType === 'success')
            return this.msgBoxMsgSuccess[this.requestStatus];
        else if (this.statusType === 'other')
            return this.msgBoxMsgOther[this.requestStatus];
        else
            return this.loadingError;
    }
    get msgBoxBtns() { return this.btns[this.requestStatus]; }
    get msgBoxContent() { return { title: this.msgBoxTitle, msg: this.msgBoxMsg || this.contentDefault, ...this.msgBoxBtns }; }
};
MsgBoxTools = __decorate([
    Mixin
], MsgBoxTools);
export default MsgBoxTools;
